
export const ChoiceScriptEntry = ({
  choiceObj, insertEntries, onNext
}) => {
  const choiceClicked = (input, inputIdx) => {
    const newEntries = choiceObj.handler({input, inputIdx});

    if (newEntries) {
      insertEntries(newEntries)
    };

    onNext()
  };

  return (
    choiceObj.options.map((choice, idx) => {
      const clickFn = () => {
        choiceClicked(choice, idx)
      }

      return <button onClick={clickFn}>
        {choice}
      </button>
    }
      
    )
  );
};
