import { TextScriptEntry } from "./TextScriptEntry";
import { ChoiceScriptEntry } from "./ChoiceScriptEntry";

export const ScriptWrapper = ({
  script, addEntry, insertEntries
}) => {

  return <div className="crtFront" id="textContainer">
    {script.map((scriptEntry) => {
      if (scriptEntry.type === 'text') {
        return <TextScriptEntry
          key={scriptEntry.id}
          onNext={addEntry}
          text={scriptEntry.content} />;
      } else {
        return <ChoiceScriptEntry
          choiceObj={scriptEntry}
          insertEntries={insertEntries}
          onNext={addEntry}
          key={scriptEntry.id} />;
      }
    }

    )}
  </div>;

};
