import logo from './logo.svg';
import './App.css';
import './css/crt.css';
import './css/main.css';
import d1Dict from './scriptFiles/d1dict.js'  


import {useState, useEffect} from 'react';
import {nanoid} from 'nanoid'
import { ScriptWrapper } from './components/ScriptWrapper';

const dict = [...d1Dict.script]

function App(props) {
  const [script, setScript] = useState([])
  const [isStarted, setIsStarted] = useState(false)

  function addScriptEntry() {
        let scriptEntry = dict.shift();

        if (!scriptEntry) return; 

        if (typeof scriptEntry === 'string') {
          scriptEntry = {type: 'text', content: scriptEntry}
        }

        scriptEntry.id = nanoid()

        const newScript = [...script, scriptEntry]
        setScript(newScript);

  }

  function insertEntries(entries) {
    // if (curIdx < d1Dict.script.length - 1) {
        dict.unshift(...entries);
    // }
  }

  function start() {
    addScriptEntry();
    setIsStarted(true)
  }

  useEffect(() => {
    const interval = setInterval(() => {
      

      document.querySelector("#textContainer").scrollTo(0,document.querySelector("#textContainer").scrollHeight);
    }, 250)

    return () => clearInterval(interval);

    // function onNext() {
    //   if (curIdx < d1Dict.script.length - 1) {
    //     const scriptEntry = d1Dict.script[curIdx++];
    //     const newScript = [...script, {content: scriptEntry, onNext: () => onNext()}]
    //     setScript(newScript);
    //   }

    //   // document.querySelector("#textContainer").scrollTo(0,document.querySelector("#textContainer").scrollHeight);
    // }

      

  }, [script]);
  

  return (
    <div className="App">
      <p id="title">Garfield</p>
      <button onClick={start} style={{visibility: isStarted?'hidden':'visible'}}>start</button>
      <header className="App-header container crt">
        { <ScriptWrapper script={script} addEntry={addScriptEntry} insertEntries={insertEntries}></ScriptWrapper>  }
        <div id="anchor"></div>
      </header>
    </div>
  );
}

export default App;
